.promotion-container {
    margin: 4rem 0;
    padding: 3rem 5rem;
}

.promotion-card {
    text-align: center;
    padding: 5rem 4rem;
}
.promotion-card .card-title {
    font-size: x-large;
    line-height: 0.75;
}
.promotional-button {
    margin: 1rem 0;
}

@media screen and (max-width: 800px) {
    .promotion-container {
        padding: 0 2rem;
    }
    .promotion-card {
        padding: 2rem 0.5rem;
        width: 100%;
        height: auto;
    }
    .promotion-card .card-title {
        font-size: large;
        line-height: 1.5rem;
    }
    .btn {
        max-width: 100%;
        font-size: small;
        font-weight: bolder;
    }
}