#product {
    background-color:azure;
}
.left {
    background-color: lightgrey;
}

.product-content {
    display: flex;
    width: 100%;
    padding: 1rem 0;
}

.product-title {
    padding: 20% 3rem;
    width: 500%;
    text-align: left;
}

.product-image img {
    margin: auto;
    max-width: 100%;
    max-height: 100%;
}

.product-title h3 {
    font-weight: 600;
    font-size: 34px;
    line-height: 40px;
}

.product-title p {
    text-align: justify;
}

.product-link {
    display: inline-block;
    padding: 1rem;
    text-align: center;
}

.product-link a {
    font-weight: 300;
    font-size: 16px;
    padding: 1rem 3rem;
    color: black;
    text-decoration: underline;
}

@media screen and (max-width: 600px) {
    .product-content {
        display: block;
        min-height: 40vh
    }
    .product-title {
        text-align: center;
        padding: 2rem 4rem;
    }
    .product-image {
        padding: 1rem 0;
    }
    .product-link a {
        padding: 0 .5rem;
        font-size: 15px;
        display: inline-block;
        justify-content: space-between;
    }
    
}