.signin-body {
    background-color: black;
    min-height: 100vh;
    padding-top: 4rem;
}

.card.signin-card {
    padding: 10px 0px 20px;
    background-color:black;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
    --webkit-box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
    --moz-box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
    
}

img.signin-img{
    padding: 0 2rem 2rem;
    margin: auto;
    display: block;
    height: 200px;
}

form.signin-form {
    max-width: 300px;
    margin: auto;
}


a.signin-help-link {
    padding-top: 1rem;
    text-align: center;
    display: block;
}

button.signin-btn {
    color: black;
    background-color: white;
    width: 100%;
    margin-bottom: 1rem;
}

.signin-check {
    padding: 1rem 0rem;
    color: white;
    text-align: center;
}
