#registration {
    padding: 5rem 0;
}
.registration-form .btn {
    margin: auto;
    justify-content: center;
    display: flex;
    width: 100%;
    padding: 1rem 0;
}

.section-title h2 {
    padding: 2rem 0;
}

.register-page {
    padding: 3rem 0;
}

@media screen and (max-width: 800px) {
    .register-page {
        padding: 0 2rem;
    }
    .section-title h2 {
        padding: 3rem 0;
    }
    .registration-form {
        padding: 1rem 0;
    }

}