#header {
    background-color: lightgray;
    padding: 5rem 0 0 0;
}

.header-info {
    padding: 20% 3rem;
    height: 70vh;
}

.header-image {
    display: flex;
    margin: auto;
    max-width: 100%;
    height: auto;
    vertical-align: middle;
    padding: 1rem 0;
}

.header-title {
    font-size: 35px;
    font-weight: bolder;
    vertical-align: middle;
    padding: 0.5rem 0;
}

.header-call-to-action {
    background-color: white;
    text-align: center;
    padding: 3rem 6rem;
    font-weight: 600;
}

.header-button  {
    background-color: black;
}
.header-button a {
    color: white;
    text-decoration: none;
}

@media screen and (max-width: 900px) {
    .header-info {
        height: 110vh;
        padding: 10% 1rem;
    }
}

@media screen and (max-width: 600px) {
    .header-info {
        height: 55vh;
    }
}