#support {
    margin-top: 5rem;
}
.support-card {
    margin-bottom: 3rem;
}
.support-card ul li {
    margin-left: 1rem;
    text-align: left;
}

.support-card ol li {
    margin-left: 2rem;
    /* text-decoration: underline; */
    cursor: pointer;
}

.card-title {
    font-size: x-large;
    margin: 1rem 0 1rem 0;
    text-align: center;
}

.card-body {
    padding-top: 0;
}

.support-card .card-text {
    text-align: justify;
}

@media screen  and (max-width: 800px){
    .support-row {
        padding: 0 1rem;
    }

}